export default {
	props:{
		city:{
			type:Array,
			default:[]
		},
		letter:{
			type:String,
			default:''
		},
	},
	data() {
		return {
			list:['北京','深圳','上海','南京','广州','香港','武汉','重庆','西安','成都','昆明','大连'],
			viewId:'',
			cityName:'北京'
		};
	},
	onshow() {
		uni.getStorage({
			key:'city',
			success:(res)=>{
				if(res.data.length>0){
					this.cityName = res.data
				}
			}
		})
	},
	methods:{
		click(res){
			uni.setStorage({
				key:'city',
				data:res
			})
			uni.getStorage({
				key:'city',
				success:(res)=>{
					if(res.data.length>0){
						this.cityName = res.data
					}
				}
			})
			uni.redirectTo({
				url:'/pages/city/city'
			})
		}
	},
	watch:{
		letter(){
			console.log(this.letter);
			this.viewId = this.letter
		}
	}
}
